import React, { Fragment, useRef } from 'react';
import { parseAbsoluteToLocal } from '@internationalized/date';
import { useController } from 'react-hook-form';
import { mergeProps } from '@react-aria/utils';
import { useDatePickerState } from './use-date-picker-state';
import { DialogTrigger } from '@ui/overlays/dialog/dialog-trigger';
import { DateRangeIcon } from '@ui/icons/material/DateRange';
import { Dialog } from '@ui/overlays/dialog/dialog';
import { DialogBody } from '@ui/overlays/dialog/dialog-body';
import { Calendar } from '../calendar/calendar';
import { DatePickerField } from '../date-range-picker/date-picker-field';
import { DateSegmentList } from '../segments/date-segment-list';
import { useDateFormatter } from '@ui/i18n/use-date-formatter';
import { useTrans } from '@ui/i18n/use-trans';
import clsx from 'clsx';
import { DialogFooter } from '@ui/overlays/dialog/dialog-footer';
import { Button } from '@ui/buttons/button';
import { Trans } from '@ui/i18n/trans';
import { useCurrentDateTime } from '@ui/i18n/use-current-date-time';
export function DatePicker({
  showCalendarFooter,
  ...props
}) {
  const state = useDatePickerState(props);
  const inputRef = useRef(null);
  const now = useCurrentDateTime();
  const footer = showCalendarFooter && <DialogFooter padding="px-14 pb-14" startAction={<Button disabled={state.isPlaceholder} variant="text" color="primary" onClick={() => {
    state.clear();
  }}>
          <Trans message="Clear" />
        </Button>}>
      <Button variant="text" color="primary" onClick={() => {
      state.setSelectedValue(now);
      state.setCalendarIsOpen(false);
    }}>
        <Trans message="Today" />
      </Button>
    </DialogFooter>;
  const dialog = <DialogTrigger offset={8} placement="bottom-start" isOpen={state.calendarIsOpen} onOpenChange={state.setCalendarIsOpen} type="popover" triggerRef={inputRef} returnFocusToTrigger={false} moveFocusToDialog={false}>
      <Dialog size="auto">
        <DialogBody className="flex items-start gap-40" padding={showCalendarFooter ? 'px-24 pt-20 pb-10' : null}>
          <Calendar state={state} visibleMonths={1} />
        </DialogBody>
        {footer}
      </Dialog>
    </DialogTrigger>;
  const openOnClick = {
    onClick: e => {
      e.stopPropagation();
      e.preventDefault();
      if (!isHourSegment(e)) {
        state.setCalendarIsOpen(true);
      } else {
        state.setCalendarIsOpen(false);
      }
    }
  };
  return <Fragment>
      <DatePickerField ref={inputRef} wrapperProps={openOnClick} endAdornment={<DateRangeIcon className={clsx(props.disabled && 'text-disabled')} />} {...props}>
        <DateSegmentList segmentProps={openOnClick} state={state} value={state.selectedValue} onChange={state.setSelectedValue} isPlaceholder={state.isPlaceholder} />
      </DatePickerField>
      {dialog}
    </Fragment>;
}
export function FormDatePicker(props) {
  const {
    min,
    max
  } = props;
  const {
    trans
  } = useTrans();
  const {
    format
  } = useDateFormatter();
  const {
    field: {
      onChange,
      onBlur,
      value = null,
      ref
    },
    fieldState: {
      invalid,
      error
    }
  } = useController({
    name: props.name,
    rules: {
      validate: v => {
        if (!v) return;
        const date = parseAbsoluteToLocal(v);
        if (min && date.compare(min) < 0) {
          return trans({
            message: 'Enter a date after :date',
            values: {
              date: format(v)
            }
          });
        }
        if (max && date.compare(max) > 0) {
          return trans({
            message: 'Enter a date before :date',
            values: {
              date: format(v)
            }
          });
        }
      }
    }
  });
  const parsedValue = value ? parseAbsoluteToLocal(value) : null;
  const formProps = {
    onChange: e => {
      onChange(e ? e.toAbsoluteString() : e);
    },
    onBlur,
    value: parsedValue,
    invalid,
    errorMessage: error?.message,
    inputRef: ref
  };
  return <DatePicker {...mergeProps(formProps, props)} />;
}
function isHourSegment(e) {
  return ['hour', 'minute', 'dayPeriod'].includes(e.currentTarget.ariaLabel || '');
}